import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, message, InputNumber } from 'antd';
import {
  getYearEducationById,
  updateYearEducation,
  createYearEducation,
} from 'services/yearEducationService';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formzone' })
class FormYearEducationPage extends Component {
  state = {
    YearEducation: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ YearEducation: true, YearEducationId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getYearEducationById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { YearEducationId } = this.state;
    try {
      if (!YearEducationId) {
        await createYearEducation(values);
        message.success('Tạo thành công');
      } else {
        await updateYearEducation({ id: YearEducationId, ...values });
        message.success('Chỉnh sửa thành công');
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  changeFromYear = number => {
    const { initialValue } = this.state;
    initialValue.toYear = number + 1;
    this.setState({
      initialValue,
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { YearEducation } = this.state;
    return (
      <Card title={`${YearEducation ? 'Cập nhật' : 'Thêm'} khu vực`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            {YearEducation ? (
              <Form.Item style={{ display: 'none' }} hasFeedback label="Từ năm" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<InputNumber />)}
              </Form.Item>
            ) : null}
            <Form.Item hasFeedback label="Từ năm" {...formItemLayout}>
              {getFieldDecorator('fromYear', {
                initialValue: initialValue.fromYear,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber style={{ width: '200px' }} onChange={this.changeFromYear} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến năm" hasFeedback>
              {getFieldDecorator('toYear', {
                initialValue: initialValue.toYear,
              })(<InputNumber style={{ width: '200px' }} disabled />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormYearEducationPage {...props} />
  </Page>
));



